
@media print {
  .Menu {
    display: none;
  }
  hr {
    display: none;    
  }
}

.Menu {
  margin: 0px;
  color: blue;
  background-color: white;
  font-size: 16px;
  font-weight: bold;
  position: sticky;
  z-index: 1;
  top: 0;
  padding-inline-start: 0px;
}

hr {
    margin: 1px;
}

.Menu-selected {
  color: blue;
  background-color: lightGrey;
}

.Menu-red {
  color: red;
}
